<script>
// 
export default {
  data() {
    return {
      products: [],
      product: {},
      // modo
      status: {
        loadingItem: '',
        // id
      },
      form: {
        user: {
          email: '',
          tel: '',
          address: '',
        },
        message: '',
      },
      order: {
        user: {},
      },
      orderId: '',
      // orderId
      cart: {
        carts: []
      },
    };
  },
  created() {
    this.orderId = this.$route.params.orderId;
    // idorderIdindex.js)
    console.log(this.orderId);
  },
  methods: {
    getOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order/${vm.orderId}`;
      vm.isLoading = true;

      this.$http.get(url).then((rs) => {
        console.log(rs);
        vm.order = rs.data.order;
        vm.isLoading = false;
      });
    },
    removeCartItem(id) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`;
      vm.isLoading = true;
      this.$http.delete(url).then(() => {
        vm.getCart();
        vm.isLoading = false;
      });
    },
    addtoCart(id, qty = 1) {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.status.lodingItem = id;
      const cart = {
        product_id: id,
        qty,
      }
      this.$http.post(url, { data: cart }).then((response) => {
        console.log(response);
        vm.status.lodingItem = '';
        vm.getCart();
        $('#productModal').modal('hide');
      });
    },
    getCart() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`;
      vm.isLoading = true;
      this.$http.get(url).then((response) => {
        vm.cart = response.data.data;
        console.log(response);
        vm.isLoading = false;
      });
    },
    createOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`;
      const order = vm.form;
      vm.isLoading = true;
      this.$http.post(url, { data: order }).then((rs) => {
        console.log('', rs);
        if (rs.data.success) {
          vm.$router.push(`/customer_checkout/${rs.data.orderId}`)
          // router
        }
        vm.isLoading = false;
      });
    },
    payOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/pay/${vm.orderId}`;
      vm.isLoading = true;

      this.$http.post(url).then((rs) => {
        console.log(rs);
        vm.isLoading = false;
      });
    }
  },

}

</script>


